<template>
  <div class="content-div home-div" id="content-div">
    <transition name="fade">
      <div class="window-box-cover" @click="closeBox" v-if="noticestatus">
        <div class="window-box">
          123
        </div>
      </div>  
    </transition>
    <transition name="fade">
      <div class="window-box-cover" @click="closeBox" v-if="userstatus">
        <div class="window-box text-center py-3">
          <img src="https://member.aorus.com/images/pic-default.jpg" class="userImg mb-3" alt="">
          <h4>{{user_name}}</h4>
          <h5 class="text-muted">{{user_email}}</h5>
          <button class="btn btn-outline-light mt-3" @click="logout">Logout</button>
        </div>
      </div>  
    </transition>
    <div class="home-div-cover">
      <div v-for="i in 1000" :key="i">{{watermark}}</div>
    </div>
    <transition name="fade">
      <Welcome class="page" v-if="pageData[0].status"/>
    </transition>
    <transition name="fade">
      <PowerBi class="page" v-if="pageData[1].status"/>
    </transition>
    <transition name="fade">
      <CustomerProfile class="page" v-if="pageData[2].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[3].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[4].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[5].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[6].status"/>
    </transition>
    <transition name="fade">
      <SalesInsight class="page" v-if="pageData[7].status"/>
    </transition>
  </div>
</template>

<script>
import Welcome from '../components/page/Welcome'
import SalesInsight from '../components/page/SalesInsight'
import CustomerProfile from '../components/page/CustomerProfile'
import PowerBi from '../components/page/PowerBi'
import Page404 from '../components/page/page404'

export default {
  components: {
    Welcome,
    SalesInsight,
    CustomerProfile,
    PowerBi,
    Page404,
  },
  props: {
    nowstatus: String,
    noticestatus: Boolean,
    userstatus: Boolean
  },
  computed: {
    user_name(){
      return this.$store.state.user_name;
    },
    user_email(){
      return this.$store.state.user_email;
    }
  },
  watch:{
		nowstatus(){
      this.changeHome(this.nowstatus);
    }
	},
  mounted:function(){
    this.initHeight();
    this.initStatus(this.nowstatus)
    window.onresize = () => {
      this.initHeight();
    }
  },
  data(){
    return{
      pageStatus: this.nowstatus, 
      watermark: this.$store.state.watermark,
      pageData: this.$store.state.mainPageData,
    }
  },
  methods:{
    initHeight(){
      var h = window.innerHeight - 50;
      document.getElementById('content-div').setAttribute("style","height:"+h+"px");
    },
    initStatus(id){
      this.changeHome(id);
    },
    changeHome(id){
      for(let key in this.pageData){
        if(id == this.pageData[key].id){
          this.pageData[key].status = true;
        }else{
          this.pageData[key].status = false;
        }
      }
    },
    closeBox(){
      this.$emit('userStatus',false);
      this.$emit('noticeStatus', false);
    },
    logout(){
      this.$store.state.login_status = "active";
      this.$store.state.main_status = "0";
      this.changeHome('0')
      //主畫面
      this.$store.state.main_status= '0';
      this.$store.state.mainId= 0;
      //次頁面
      this.$store.state.page_status= ''; 
      this.$store.state.pageId= 0;
      //子頁面
      this.$store.state.subpage_status= '';
      this.$store.state.subPageId= 0;
      this.$store.state.subPageDataId= 0;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content-div{
  position: relative;
  width: 100%;
  height: calc(100vh - 50px);
  overflow-y: auto;
  overflow-x: hidden;
  background: #4e505f;
  /* background: linear-gradient(90deg,#212335,#4e505f,#212335); */
  background: linear-gradient(180deg, #43648B 0%, #6FA1D1 48.44%, #2F76B8 100%);
}

.page{
  position: absolute;
}

.fade-enter-active, .fade-leave-active {
  transition: 0.7s all;
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.home-div-cover{
    position: fixed;
    z-index: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 50px;
    overflow: hidden;
}

.home-div-cover div{
    position: relative;
    display: inline-block;
    width: 70px;
    text-align: center;
    line-height: 18px;
    font-size: 14px;
    /* font-weight: 700; */
    color: rgba(255,255,255,0.05);
    /* transform: rotate(30deg); */
}



.window-box-cover{
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0px;
  width: 100%;
  height: calc(100% - 0px);
  background: rgba(0,0,0,0.7);
}
.window-box{
  position: relative;
  margin: 0 auto;
  margin-top: calc(50vh - 25vh);
  /* display: inline-block; */
  width: calc(100% - 30px);
  max-width: 500px;
  background: rgba(62, 65, 79,1);
  border-radius: 5px;
  padding: 15px;
  min-height: 50vh;
}

.userImg{
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 2px solid #fff;
  object-fit: cover;
}

</style>
